import axios from 'axios';
require('dotenv').config();

class AuthService {
  login(user) {
    return axios
      .post(process.env.VUE_APP_SECURITY_SERVICE +'/api/auth/signin', {
        username: user.username,
        password: user.password
      })
      .then(response => {
        if (response.data.accessToken) {
          localStorage.setItem('user', JSON.stringify(response.data));
        }

        return response.data;
      });
  }

  logout() {
    localStorage.removeItem('user');
  }

  getUsers() {
    let user = JSON.parse(localStorage.getItem('user'));
    return axios
      .get(process.env.VUE_APP_SECURITY_SERVICE +'/api/auth/user', {
        headers: {
          Authorization: 'Bearer ' + user.accessToken
        }
      }).then(response => response.data)
  }

  saveUser(formData) {
    let user = JSON.parse(localStorage.getItem('user'));
    return axios
      .post(
        process.env.VUE_APP_SECURITY_SERVICE +'/api/auth/signup',
         formData,
        {
          headers: {
            Authorization: 'Bearer ' + user.accessToken
          }
        }
      )
      .then(response => response.data)
  }

  updateUser(formData) {
    let user = JSON.parse(localStorage.getItem('user'));
    return axios
      .put(
        process.env.VUE_APP_SECURITY_SERVICE +'/api/auth/user',
         formData,
        {
          headers: {
            Authorization: 'Bearer ' + user.accessToken
          }
        }
      )
      .then(response => response.data)
  }

  deleteUser(formData) {
    let user = JSON.parse(localStorage.getItem('user'));
    return axios
      .delete(
        process.env.VUE_APP_SECURITY_SERVICE +'/api/auth/user/'+formData,
        {
          headers: {
            Authorization: 'Bearer ' + user.accessToken
          }
        }
      )
      .then(response => response.data)
  }
}

export default new AuthService();
