import Vue from 'vue';
import Router from 'vue-router';
import Login from './views/Login.vue';
import Constants from "./plugins/Constants.js";

Vue.use(Constants);
Vue.use(Router);

export const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/login',
      component: Login
    },
    {
      path: '/home',
      name: 'Home',
      component: () => import('./views/Home.vue')
    },
    {
      path: '/dashboard',
      name: 'Dashboard',
      component: () => import('./views/DashBoard.vue')
    },
    {
      path: '/dashboard/results',
      name: 'DashboardResults',
      component: () => import('./views/DashBoardResults.vue')
    },
    {
      path: '/job',
      component: () => import('./components/Crud.vue'),
      props: { current: 'job-dynamic' , 
      mainTableFields: Vue.prototype.$constants().jobTableFields,
      type: 'job'
      }
    },
    {
      path: '/inspection',
      component: () => import('./views/Inspection.vue'),
      props: { current: 'job-dynamic' , 
      mainTableFields: Vue.prototype.$constants().jobTableFields,
      type: 'job'
      }
    },
    {
      path: '/customer',
      component: () => import('./components/Crud.vue'),
      props: { current: 'customer-dynamic' , 
      mainTableFields:  Vue.prototype.$constants().customerTableFields, 
      type: 'customer'
      }
    },
    {
      path: '/site',
      component: () => import('./components/Crud.vue'),
      props: { current: 'site-dynamic' , 
      mainTableFields:  Vue.prototype.$constants().siteTableFields, 
        type: 'site'
      }
    },
    {
      path: '/user',
      component: () => import('./components/Crud.vue'),
      props: { current: 'user-dynamic', 
      mainTableFields: Vue.prototype.$constants().userTableFields , 
        type: 'user'
      }
    }
  ]
});
