<template>
  <div>
    <div class="main" v-if="currentUser">
      <b-navbar
      >
        <b-navbar-nav class="ml-auto">
          <!--<b-nav-item-dropdown text="Lang" class="ml-auto mr-3">
            <b-dropdown-item href="#">EN</b-dropdown-item>
            <b-dropdown-item href="#">ES</b-dropdown-item>
          </b-nav-item-dropdown>-->
          <b-nav-item class="mr-5">
            <i class="fas fa-user mr-1"></i><b>{{ currentUser.username }}</b>
          </b-nav-item>
          <b-nav-item @click.prevent="logOut">
            <i class="fas fa-sign-out-alt mr-1"></i>Log out
          </b-nav-item>
        </b-navbar-nav>
      </b-navbar>
   
      <sidebar-menu v-if="currentUser.roles[0]=='ROLE_ADMIN'"
        :menu="sideBarMenuAdmin"
        width="180px"
        :collapsed="sideBarCollapsed"
        @toggle-collapse="onToggleCollapse"
      >
      </sidebar-menu>
      <sidebar-menu v-if="currentUser.roles[0]=='ROLE_INSPECTOR'"
        :menu="sideBarMenuInspector"
        width="180px"
        :collapsed="sideBarCollapsed"
        @toggle-collapse="onToggleCollapse"
      >
      </sidebar-menu>
      <b-card
        class="card-form"
        v-bind:style="[
          sideBarCollapsed == true
            ? 'margin: 10px 30px 30px 75px; padding: 0px 0px 0px 0px;'
            : 'margin: 10px 30px 30px 205px;padding: 0px 0px 0px 0px;'
        ]"
      >
        <router-view :key="$route.fullPath"></router-view>
      </b-card>
    </div>
    <div v-if="!currentUser">
      <router-view v-if="!currentUser" :key="$route.fullPath"></router-view>
    </div>
  </div>
</template>

<script>
  
export default {
  data() {
    return {
      sideBarCollapsed: false,
      sideBarMenuAdmin: [ {
          header: 'Main menu',
          hiddenOnCollapse: false
        },
        { href: '/job', title: 'Jobs', icon: 'fa  fa-code-branch' },
        {
          href: '/inspection',
          title: 'Inspections',
          icon: 'fa  fa-search'
        },
        { href: '/customer', title: 'Customers', icon: 'fa  fa-user-tie' },
        { href: '/site', title: 'Sites', icon: 'fa  fa-sitemap' },
        {
          title: 'Accounts',
          icon: 'fa  fa-users',
          child: [
            {
              href: '/user',
              title: 'Users',
              icon: 'fa  fa-user'
            }
          ]
        },
        { href: '/dashboard', title: 'Dashboard', icon: 'fas  fa-chart-pie' ,
        child: [
            {
              href: '/dashboard/results',
              title: 'Results',
              icon: 'fa  fa-table'
            }
          ]
        }],
        sideBarMenuInspector: [ {
            header: 'Main menu',
            hiddenOnCollapse: false
          },
          {
            href: '/inspection',
            title: 'Inspections',
            icon: 'fa  fa-search'
          }
        ]

    };
  },
  computed: {
    currentUser() {
      return this.$store.state.auth.user;
    }
  },
  methods: {
    generateSideBarMenu()
    {
      this.sideBarMenu = [
     
      ];
      this.$forceUpdate();
     
    },
    sidbarStateChange() {
      this.sideBarCollapsed = !this.sideBarCollapsed;
    },
    onToggleCollapse(collapsed) {
      this.sideBarCollapsed = collapsed;
    },
    logOut() {
      this.$store.dispatch('auth/logout');
      this.$router.push('/login');
    },
    catchError(error) {
      if (error.response.status === 401) {
        this.$store.dispatch('auth/logout');
        this.$router.push('/login');
      } else {
        console.log(error);
        this.showMsgBoxConfirm(
          'A problem occurred. Please contact the administrator',
          'danger',
          'Error',
          'sm'
        );
      }
    },
    showMsgBoxConfirm(msg, vari, title, size) {
      this.$bvModal
        .msgBoxOk(msg, {
          title: title,
          size: size,
          buttonSize: 'sm',
          okVariant: vari,
          headerClass: 'p-2 border-bottom-0',
          footerClass: 'p-2 border-top-0',
          centered: true
        })
        .then(value => {
          this.msgValue = value;
        });
    }
  },
  mounted() {

  }
};
</script>
<style>
@font-face {
  font-family: 'Sans';
  src: local('Sans'), url(./fonts/ProductSans-Regular.ttf) format('truetype');
}
.main {
  font-family: 'Sans';
  font-size: 14px;
  /*width: 100vw;
  height: 100vh;*/
  color: #000;
  /*background-image: linear-gradient(#eae8e8, #ebe9e9);*/
}
body
{
  background-image: linear-gradient(#eae8e8, #ebe9e9);
}
.card-login {
  max-width: 350px;
  background-color: #ffffff;
  margin: 150px auto 25px;
  border-radius: 20px;
  border-color: #dddddd;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 3px 3px -2px rgba(0, 0, 0, 0.14),
    0 1px 8px 0 rgba(0, 0, 0, 0.12);
}
.card-form {
  background-color: #f7f7f7;
  margin: 0px 0px 0px 0px;
  padding: auto;
  border-color: #ffffff;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 3px 3px -2px rgba(0, 0, 0, 0.14),
    0 1px 8px 0 rgba(0, 0, 0, 0.12);
}
.actionButton {
  border-color: #ffffff;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 3px 3px -2px rgba(0, 0, 0, 0.14),
    0 1px 8px 0 rgba(0, 0, 0, 0.12);
}
.actionButtonCheck {
  border-color: #ffffff;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 3px 3px -2px rgba(0, 0, 0, 0.14),
    0 1px 8px 0 rgba(0, 0, 0, 0.12);
  width: 100px;
  height: 100px;
  margin: 10px 20px 10px 20px;
}
.gradient {
  background-image: linear-gradient(#222222, #2a2a2e);
}
.qtyField {
  max-width: 80px;
}
</style>
