<template>
  <div class="card-body text-secondary text-center card-login" >
    <img
      class="card-img-top logo mt-3"
      style="width:200px; heigth: 100px"
      src="../images/AKgroup_flat.jpg"
    />
    <p style="margin:10px 0px 0px 0px;font-size: 160%;">
      Sign in
    </p>
    <p style="font-size: 80%;">
      with your username and password
    </p>
    <form name="form">
      <div class="form-group mt-4">
        <input
          v-model="user.username"
          v-validate="'required'"
          type="text"
          class="form-control"
          name="username"
          placeholder="User"
        />
        <div
          v-if="errors.has('username')"
          class="alert alert-danger"
          role="alert"
        >
          User required
        </div>
      </div>
      <div class="form-group">
        <input
          v-model="user.password"
          v-validate="'required'"
          type="password"
          class="form-control"
          name="password"
          placeholder="Password"
        />
        <div
          v-if="errors.has('password')"
          class="alert alert-danger"
          role="alert"
        >
          Password required
        </div>
      </div>
      <div class="form-group d-flex justify-content-end">
        <b-button
          v-on:click="handleLogin"
          variant="primary"
          class="mt-3 actionButton"
          :disabled="loading"
        >
          <span
            v-show="loading"
            class="spinner-border spinner-border-sm "
          ></span>
          <span>Log in</span>
        </b-button>
      </div>

      <div class="form-group">
        <div v-if="message" class="alert alert-danger" role="alert">
          {{ message }}
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import User from '../models/user';

export default {
  name: 'Login',
  data() {
    return {
      user: new User('', ''),
      loading: false,
      message: ''
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    }
  },
  created() {
    if (this.loggedIn) {
      this.$router.push('/home');
    }
  },
  methods: {
    handleLogin() {
      this.loading = true;
      this.$validator.validateAll().then(isValid => {
        if (!isValid) {
          this.loading = false;
          return;
        }

        if (this.user.username && this.user.password) {
          this.$store.dispatch('auth/login', this.user).then(
            () => {
              this.$router.push('/home');
            },
            error => {
              this.loading = false;

              if (error.response.status != 401) {
                this.message =
                  (error.response &&
                    error.response.data &&
                    error.response.data.message) ||
                  error.message ||
                  error.toString();
              } else {
                this.message = 'Incorrect user or password';
              }
            }
          );
        }
      });
    }
  },
  components: {}
};
</script>

<style></style>
